@media (max-width: 1680px) {
  .homeStart {
    height: 50rem;
  }

  .home-section {
    justify-content: left;
    height: 40rem;
  }

  .portfolio-text {
    line-height: 2.1rem;
    margin-top: 15rem;
    margin-left: 4rem;
    position: relative;
    justify-content: center;
  }

  .portfolio-title {
    font-size: 4rem;
    margin-bottom: 0rem;
  }

  .portfolio-link {
    font-size: 1.5em;
    margin-left: .3rem;
  }

  .about {
    margin-top: 0rem;
    margin-left: 1.2rem;
    align-items: left;
    justify-content: left;
  }

  .about-p {
    height: 25rem;
  }
}

@media (max-width: 1222px) {
  .homeStart {
    height: 35rem;
  }
}

/* Mobile Landscape */
@media (max-width: 870px) {
  .homeStart {
    height: 30rem;
  }

  .home-section {
    justify-content: left;
    height: 40rem;
  }

  .portfolio-text {
    line-height: 2.1rem;
    margin-top: 15rem;
    margin-left: 4rem;
    position: relative;
    justify-content: center;
  }

  .portfolio-title {
    font-size: 4rem;
    margin-bottom: 0rem;
  }

  .portfolio-link {
    font-size: 1.5em;
    margin-left: .3rem;
  }

  .about {
    margin-top: 0rem;
    margin-left: 1.2rem;
    align-items: left;
    justify-content: left;
  }

  .about-p {
    height: 25rem;
  }
}
